@value mobile, smartphone, tablet, desktop from "../../css-variables/media.css";

.grid {
  display: grid;
  gap: 24px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

@media tablet {
  .grid {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}

@media desktop {
  .grid {
    gap: 48px;
  }
}
